.HomeContainer {
  margin-top: 69px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.HomeContainer img {
  width: 100%;
  filter: brightness(70%);
  object-fit: cover;
  object-position: 50% 50%;
}

.HomeContnts {
  width: 80%;
  opacity: 0;
  z-index: 1;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.HomeContntsScrolled {
  opacity: 1;
  transition: 5s;
}

.HomeTitle {
  letter-spacing: 0.06em;
  font-size: 119px;
  font-weight: bold;
  color: #ffffff;
  text-align: center;
  margin-bottom: 40px;
}

.HomeDesc {
  letter-spacing: 0.45em;
  font-size: 26px;
  font-weight: bold;
  color: #ffffff;
  text-align: center;
  margin-bottom: 70px;
}

.HomeBtn {
