}

.HomeBtn:hover {
  cursor: pointer;
  background-color: #257a6c;
  transition: 500ms;
}

.HomeBtn a {
  font-size: 16px;
  line-height: 44px;
  color: #ffffff;
  text-decoration: none;
}

.ContactContainer {
