  box-sizing: border-box;
  position: relative;
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, .5);
  background-color: #fff;
  border-radius: 10px;
  width: 614px;
  top: 50%;
  transform: translateY(-50%);
  margin: 0 auto;
  padding: 45px 30px 40px;
}
.reserveModalInner h2 {
  font-size: 30px;
  font-weight: 600;
