}
.reserveModalInner ul li {
  margin-bottom: 30px;
}
.reserveModalInner ul li:first-child p:last-child {
  margin-top: 17px;
  font-size:1.3rem;
  font-weight: bold;
}
.reserveModalInner ul li p {
  font-size: 17px;
}
.reserveModalInner ul li input {
  font-size: 20px;
  width: 100%;
  padding: 15px 0;
  color: #363636;
  border: none;
  border-bottom: 2px solid #363636;
  outline: none;
}
.reserveModalInner ul li select {
  color: #363636;
  padding: 5px 0;
  margin-top: 15px;
  border: none;
  border-bottom: 2px solid #363636;
  outline: none;
}
.reserveModalInner form > input {
  width: 100%;
  height: 75px;
  font-size: 22px;
  border: 2px solid #363636;
  color: #363636;
  cursor: pointer;
}


