}
.userDescList {
  display: flex;
}
.userDescList p {
  padding: 0 20px;
}
.userDescList p:first-child {
  border-right: 1px solid #05AA88;
}
.userDescList p span {
  display: block;
  font-size: .9rem;
  color: #99ecdd;
  letter-spacing: -.3px;
  text-align: center;
  white-space: nowrap;
}
.userDescList p em {
  display: block;
