/* --userInfoBottom-- */
.userInfoBottom {
  margin-top: 15px;
}
.profileChangeBtn {
  width: 100%;
  height: 38px;
  border-radius: 8px;
  background: #0cc5a4;
  box-shadow: 0 2px 5px 0 rgb(0 107 88 / 10%);
  box-sizing: border-box;
