  }

  .infoBox div {
    width: 90%;
    font-size: 13px;
    height: auto;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }

  .iconBox1 {
    display: flex;
    padding-top: 15%;
  }

  .iconBox1 i {
    flex: 1 1;
    font-size: 25px;
    text-align: center;
  }

  .iconBox2 {
    display: flex;
    padding-top: 5%;
  }

  .iconBox2 span {
    flex: 1 1;
    font-size: 7.5px;
    text-align: center;
  }

  .btnToIntro {
    margin-top: 10%;
  }
}

@media screen and (min-width: 768px) and (max-width: 1000px) {
  .container {
    height: 7 0%;
    width: 60%;
  }

  .bgContainer {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }

  .bgImage {
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-size: cover;
  }

  .iconBox1 {
    display: flex;
    padding-top: 15%;
  }

  .iconBox2 {
    display: flex;
    padding-top: 5%;
  }
}

.HeaderContainer {
  width: 100%;
  background-color: #212121;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  z-index: 9;
}

.HeaderBox {
  opacity: 0;
  width: 980px;
  height: 69px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  top: -60px;
}

.HeaderTitle {
  font-size: 28px;
  font-weight: bold;
  color: #ffffff;
  text-decoration: none;
  letter-spacing: 0.1em;
  line-height: normal;
}

.HeaderMenu {
  display: flex;
}

.NavBtn {
  padding: 15px;
  font-size: 16px;
  color: #ffffff;
  text-decoration: none;
}

.LogoutBtn {
  padding: 15px;
  font-size: 16px;
  color: #ffffff;
  cursor: pointer;
}

.active {
  color: #3eccb5;
}

.scrolled {
  opacity: 1;
  transition: 3s;
  top: 0;
}

.MenuBtn {
  display: none;
}

@media screen and (max-width: 1000px) {
  .HeaderTitle {
    margin-left: 20px;
  }
}

@media screen and (max-width: 767px) {
  .HeaderBox {
    width: 100%;
    height: 100%;
    align-items: flex-start;
    flex-direction: column;
  }

  .HeaderTitle {
    margin: 20px 0 28px 24px;
    font-size: 24px;
  }

  .HeaderMenu {
    display: none;
  }

  .HeaderMenu.active {
    width: 100%;
    display: block;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .NavBtn,
  .LogoutBtn {
    padding: 20px;
  }

  .MenuBtn {
    display: block;
    position: relative;
    color: white;
    font-size: 30px;
    height: 30px;
    top: 20px;
    right: 20px;
    cursor: pointer;
  }
}

.FooterContainer {
  width: 100%;
  height: 96px;
  background-color: #333333;
  display: flex;
  justify-content: center;
}

.FooterBox {
  width: 980px;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.FooterLetter {
  color: #bab8b8;
  padding: 10px 15px 0 15px;
}

.FooterIcon {
  margin-top: 10px;
}

.FooterIcon img {
  width: 26px;
  height: 26px;
  margin-right: 15px;
}

@media screen and (max-width: 767px) {
  .FooterBox {
    flex-direction: column-reverse;
  }

  .FooterIcon {
    margin-top: 20px;
  }

  .FooterLetter {
    padding: 0 15px 20px 15px;
  }
}

.reservation,
.profile {
  margin-top: 69px;
}

.Init {
  width: 100%;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media screen and (max-width: 767px) {
  .reservation,
  .profile {
    margin-top: 77px;
  }
}
